import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function researchAndDevelopmentToRevenueTtm(tableName: string) {
  const title = "R&D intensity (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["researchAndDevelopmentToRevenueTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "R&D as a share of sales.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about R&D intensity?`,
              value:
                info.row.original.financialData.companyKeyMetrics?.data
                  ?.researchAndDevelopmentToRevenueTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyKeyMetrics?.data
              ?.researchAndDevelopmentToRevenueTtm ?? 0) * 100
          }
        />
      )
    },
  })
}

export { researchAndDevelopmentToRevenueTtm }
